.datatable {
  height: 81%;
  padding: 0 20px;

  .cellWithImg {
    display: flex;
    align-items: center;

    .cellImg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
      margin-right: 20px;
    }
  }

  .cellWithStatus {
    padding: 5px;
    border-radius: 5px;

    &.published {
      background-color: rgba(0, 128, 0, 0.05);
      color: green;
    }
    &.saved {
      background-color: rgba(255, 217, 0, 0.05);
      color: goldenrod;
    }
    &.hidden {
      background-color: rgba(255, 0, 0, 0.05);
      color: crimson;
    }
    &.review {
      background-color: rgba(176, 224, 230, 0.05);
      color: RoyalBlue;
    }
  }

  .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;

    .viewButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: darkblue;
      border: 1px dotted rgba(0, 0, 139, 0.596);
      cursor: pointer;
    }
    
    .deleteButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      border: 1px dotted rgba(220, 20, 60, 0.6);
      cursor: pointer;
    }
  }
}
.subcategory-list {
  font-size: 14px;
}
.MuiDataGrid-renderingZone {
  max-height: none !important;
}
.MuiDataGrid-cell {
  line-height: unset !important;
  max-height: none !important;
  white-space: normal;
}
.MuiDataGrid-row{
  max-height: none !important;
}